const filterYear = ref(new Date().getFullYear());

// Generate years list
const years = ref<number[]>([]);
for (let i = -2; i <= 2; i++) {
  years.value.push(new Date().getFullYear() + i);
}

const pagination = reactive({
  currentPage: 1,
  limit: 10,
  total: 0,
});

const form = reactive({
  nhan_vien_id: '',
  loai_ngay_nghi_id: '',
